<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-09-04 10:32:52
 * @LastEditTime: 2019-09-15 17:19:10
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
    <p class="title">{{data.displayed_owned_by}}</p>

    <div class="headerimgs">
      <img class="headerimg" src="@/assets/images/CompanyLogo@3x.png" alt />
    </div>

    <p class="travelead">{{data.name}}</p>

    <p class="budget">{{data.desc1}}</p>

    <div class="travel-insurance">{{data.category_name}}</div>

    <!-- 价格 -->
    <div class="price" v-if="plan" >{{plan.price_desc ? plan.price_desc : ''}}</div>

    <div>
      <img class="box" src="@/assets/images/starrTraveLeadEssential.png" alt />
    </div>

    <!-- Single Plan和Annual Plan -->
    <div class="buttons">     
        <button class="single btn" v-for="(item,index) in this.data.plan_list" :key="index" @click="setPlan(item)" :class="{isActive:plan.period_type == item.period_type}">{{item.plan_list_name}}</button>
    </div>

    <div class="trip" v-if="plan" >{{plan.plan_list_desc ? plan.plan_list_desc : ''}}</div>

    <!-- Key Features -->
    <div>
      <p class="key">Key Features</p>
      <div class="key-content">
        <div class="features-wrap">
          <p :class="{'features': flag}">{{data.remarks}} </p>         
          <span class="more" @click="clickmore" >{{flag ? "更多" : "隐藏"}}</span>
        </div>
        
      </div>
    </div>

    <!-- Customer Service -->
    <div class="customers" @click="customers">
      <span class="starrs">
        <img class="starr" :src=this.data.cs_logo alt />
      </span>
      <span class="customer">Customer Service</span>
      <i class="cubeic-arrow"></i>
    </div>

    <!-- Coverage Details -->
    <div class="coverages" @click="coverage">
      <span class="covers">
        <img class="cover" src="@/assets/images/Confirmation of cover@3x.png" alt />
      </span>
      <span class="coverage">Coverage Details</span>
      <i class="cubeic-arrow"></i>
    </div>

    <div class="button-bar">
      <button class="quato" @click="quato">Quato</button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
  data() {
    return {
      travelInfo: {},
      more: false,
      data: "",
      flag: true,
      remarks: [],
      ischecked: 0,
      //product_id:"5d15cdc2971f320d262b8266",
      areacode:"86",
      trip:"",
      price:""
    };
  },
  computed: {
    ...mapState([ 'product_id', 'language', 'plan'])
  },
  methods: {
      ...mapMutations(['setPlan', 'reset']),
    clickmore() { 
      this.flag = !this.flag;
    },
    /* isChoose(item) {
        this.setPlan(item)
        this.ischecked = item.period_type;
        this.trip=item.plan_list_desc;
        this.price=item.price_desc;
        console.log(this.ischecked)
    }, */
    customers(){
      this.$createDialog({
        type: 'alert',
        confirmBtn:{ text: '確認', active: true, href: 'javascript:;' },       
        content:
        this.data.customer_service

        // '如有查询，请联络Starr:<a href="mailto:(852)37655566/cs.hk@starrcompanies.com">(852)37655566/cs.hk@starrcompanies.com</a>(办公时间：星期一至星期五，上午9:00至12:30；下午1:30至下午5:00)',
      }).show()
    },
    coverage(){

        this.$router.push({ path: '/CoverageDetails'})
    },
    quato() {
     
      this.$router.push({path: "/Travelinfo/next"})
    }
  },
  created() {
    console.log(this.$t('button'));
    const resetFile = ['product_info', 'plan', 'travelInfo','userInfo','protectorInfo'];
    this.reset(resetFile);
  },

  mounted() {
    console.log(this.language);
    var params = {
      language: this.language,
      product_id: this.product_id,
      phone_country_code: this.areacode
    };
     const loading = this.$createToast({
        time: 0,
        txt: 'Loading...'
      })
      loading.show()
      this.$http.post("/wesupromo/web/tpc/getProductDetails", params).then(res => {
      this.data = res.data;
      this.$nextTick(() => { loading.hide()  })
      this.setPlan(res.data.plan_list[0])
     
    });
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 34px;
  background-color: #fff;
  .title {
    color: rgb(155, 155, 155);
    font-size: 9px;
    margin-bottom: 30px;
  }
  .travelead {
    color: #000;
    font-weight: 700;
    font-size: 44px;
    line-height: 52px;
    margin-bottom: 15px;
    margin-top: 32px;
  }
  .budget {
    color: #000;
    font-weight: 600;
    font-size: 34px;
    line-height: 42px;
    margin-bottom: 10px;
  }
  .travel-insurance {
    color: rgb(142, 142, 147);
    font-size: 30px;
  }
  .headerimgs {
    width: 165px;
    height: 76px;
  }
  .headerimg {
    width: 100%;
    height: 100%;
  }
  .price {
    text-align: right;
    color: rgb(12, 129, 255);
    font-size: 35px;
    font-weight: bold;
    margin-bottom: 35px;
  }
  .box {
    border-radius: 22px;
  }

  .cubeic-arrow {
    color: rgba(0, 0, 0, 0.25);
    font-size: 32px;
    font-weight: bold;
  }
  .buttons {
    line-height: 100px;
    display: flex;
    justify-content: space-around;
    margin: 12px 0 18px 0;
  }
 
  
  .btn {
    background: rgb(224, 223, 223);
    touch-action: none;
    width: 260px;
    height: 65px;
    border-radius: 35px;
    color: rgb(177, 175, 175);
    font-weight: 550;
    font-size: 30px;
    outline: none;
    border: none;
     
  }
  .isActive {
      background: rgb(12, 129, 255);
     color: white;
    }

  
  .trip {
    color: rgb(12, 129, 255);
    font-weight: 500;
    border-bottom: 1px solid rgb(209, 208, 208);
    line-height: 65px;
  }
  .key {
    font-size: 34px;
    font-weight: 550;
    line-height: 100px;
  }
  .key-content {
    border-bottom: 1px solid rgb(209, 208, 208);
    padding-bottom: 15px;
    p {
      font-size: 30px;
      font-family: Arial, Helvetica, sans-serif;
      line-height: 45px;
    }
  }
  .features-wrap {
    white-space: pre-line;
    position: relative;
    .more {
        display: block;
        width: 100%;
        text-align: right;
        color: rgb(12, 129, 255);
      }
  }
  .features{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    /* autoprefixer:off */
    -webkit-box-orient: vertical;
    /* autoprefixer:on */
    -webkit-line-clamp:3;
}
  .customers {
    line-height: 75px;
    border-bottom: 1px solid rgb(209, 208, 208);
    padding-bottom: 15px;
    display: flex;
    justify-content: space-between;
    .starrs {
      display: inline-block;
      width: 65px;
      height: 65px;
      .starr {
        width: 100%;
        height: 100%;
        vertical-align: middle;
        margin-left: 20px;
      }
    }
    .customer {
      font-size: 35px;
      font-family: Arial, Helvetica, sans-serif;
      margin-left: -220px;
    }
  }
  .coverages {
    line-height: 75px;
    border-bottom: 1px solid rgb(209, 208, 208);
    padding-bottom: 15px;
    display: flex;
    justify-content: space-between;
    .covers {
      display: inline-block;
      width: 65px;
      height: 65px;
      .cover {
        width: 100%;
        height: 100%;
        vertical-align: middle;
        margin-left: 20px;
      }
    }
    .coverage {
      font-size: 35px;
      font-family: Arial, Helvetica, sans-serif;
      margin-left: -220px;
    }
  }
  
  .button-bar {
    text-align: center;
    margin-top: 54px;
    margin-bottom: 102px;
  }
 .quato {
    background: rgb(239, 69, 95);
    width: 230px;
    height: 75px;
    border-radius: 15px;
    color: white;
    font-weight: 550;
    font-size: 35px;
    outline: none;
    border: none;
  }
  
}
</style>


